import { CForm, CFormGroup, CInput, CLabel, CRow, CCol, CDataTable, CButton, CModal, CModalHeader, CModalBody, CSelect, CTextarea} from '@coreui/react'
import {React, useState} from 'react'

const DataRincian = () =>{
    const [modal, setModal] = useState(false)
    return(
        <>
        <CModal
        show={modal}
        size="md"
        >
            <CModalHeader closeButton style={{backgroundColor:'#02275d', color:'white'}}><h5>Tambah Rincian Barang/Jasa</h5></CModalHeader>
            <CModalBody>
                <CRow>
                    <CCol md="12">
                        <CFormGroup>
                            <CLabel>Jenis Barang</CLabel>
                            <CSelect>
                                <option value="00">--Select--</option>
                            </CSelect>
                        </CFormGroup>
                    </CCol>
                </CRow>
                <CRow>
                    <CCol md="4">
                        <CFormGroup>
                            <CLabel>Pos Tarif / HS</CLabel>
                            <CSelect>
                                <option value="00">--Select--</option>
                            </CSelect>
                        </CFormGroup>
                    </CCol>
                    <CCol md="8">
                        <CFormGroup>
                            <CLabel>Uraian Barang</CLabel>
                            <CTextarea disabled />
                        </CFormGroup>
                    </CCol>                    
                </CRow>  
                <CRow>
                    <CCol md="12">
                        <CFormGroup>
                            <CLabel>Nilai Transaksi</CLabel>
                            <CInput/>
                        </CFormGroup>
                    </CCol>
                </CRow> 
                <div className="d-flex flex-row justify-content-end">
                    <div className="mr-2"><CButton onClick={()=>setModal(false)} className="btn btn-success"><i class="far fa-save"></i></CButton></div>
                    <div className="mr-2"><CButton onClick={()=>setModal(false)} className="btn btn-danger"><i class="fas fa-times"></i></CButton></div>
                </div>                              
            </CModalBody>
        </CModal>
        <CRow>
            <CCol md="4">
                <CFormGroup>
                    <CLabel>Total Nilai Transaksi</CLabel>
                    <CInput disabled value="0.00"/>
                </CFormGroup>
            </CCol>
            <CCol md="4">
                <CFormGroup>
                    <CLabel>PPN dan/atau PPnBM Fasilitas</CLabel>
                    <CInput disabled value="0.00"/>
                </CFormGroup>
            </CCol>
            <CCol md="4">
                <CFormGroup>
                    <CLabel>PPN dan/atau PPnBM Terutang</CLabel>
                    <CInput disabled value="0.00"/>
                </CFormGroup>
            </CCol>                                    
        </CRow>
        <div className="d-flex flex-row justify-content-end mb-2">
            <div className="mr-1"><CButton onClick={()=>setModal(true)} style={{backgroundColor:'#4dbd74', color:'white'}} className="btn btn-sm"><i class="fas fa-plus"></i></CButton></div>
            <div className="mr-1"><CButton style={{backgroundColor:'#757778', color:'white'}} className="btn btn-sm"><i class="fas fa-cloud-download-alt"></i></CButton></div>  
            <div className="mr-1"><CButton style={{backgroundColor:'#4875b4', color:'white'}} className="btn btn-sm"><i class="fas fa-cloud-upload-alt"></i></CButton></div>                        
        </div>
        <CRow>
            <CCol md="12">
                <CDataTable
                addTableClasses="tablebiru"
                fields={[
                    {key:'no', label:'No'},
                    {key:'jnsbarang', label:'Jenis Barang'},
                    {key:'urbarang', label:'Uraian Barang'},
                    {key:'nilaitrx', label:'Nilai Transaksi'},
                    {key:'aksi', label:'Aksi'}
                ]}
                />
            </CCol>
        </CRow>
        </>
    )
}

export default DataRincian