import { CCard, CCardBody, CRow, CCol, CButton} from '@coreui/react'
import {React, useState} from 'react'
import {ReactComponent as Perusahaan} from '../../../assets/perusahaa.svg'
import {ReactComponent as Kontrak} from '../../../assets/kontrak.svg'
import {ReactComponent as Dokumen} from '../../../assets/dokumen.svg'
import {ReactComponent as Rincian} from '../../../assets/rincian.svg'
import {ReactComponent as InfoTerkait} from '../../../assets/infoterkait.svg'
import {ReactComponent as Checkpoint} from '../../../assets/satu.svg'
import DataPerusahaan from './dataperusahaan'
import DataKontrak from './datakontrak'
import DataDokumen from './datadokumen'
import DataRincian from './datarincian'
import DataInfoTerkait from './datainfoterkait'
import DataCheckpoint from './checkpoint'

const New = () =>{
    const [page, setPage] = useState(1)
    const [arrPage, setArrPage] = useState([<DataPerusahaan/>, <DataKontrak/>, <DataDokumen/>, <DataRincian/>, <DataInfoTerkait/>, <DataCheckpoint/>])
    return(
        <>
        <CCard style={{borderRadius:'12px', boxShadow:'0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%)'}}>
            <CCardBody>
                <div className={page != 1 ? "d-flex flex-row justify-content-center mb-3" : "d-flex flex-row justify-content-center mb-5"}>
                    <div className="d-flex flex-column mr-5 align-items-center">
                        <div onClick={()=>setPage(1)} role="button" className={page == 1 ? "d-flex justify-content-center px-2 py-2 mb-1 on" : "d-flex justify-content-center px-2 py-2 mb-1 off"} style={{borderRadius:'12px', boxShadow:'0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%)'}}>
                            <Perusahaan/>
                        </div>
                        <b>Data Perusahaan</b>                        
                    </div>  
                    <div className="d-flex flex-column mr-5 align-items-center">
                        <div onClick={()=>setPage(2)} role="button" className={page == 2 ? "d-flex justify-content-center px-2 py-2 mb-1 on" : "d-flex justify-content-center px-2 py-2 mb-1 off"} style={{borderRadius:'12px', boxShadow:'0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%)'}}>
                        <Kontrak/>                            
                        </div>
                        <b>Data Kontrak</b>                        
                    </div> 
                    <div className="d-flex flex-column mr-5 align-items-center">
                        <div onClick={()=>setPage(3)} role="button" className={page == 3 ? "d-flex justify-content-center px-2 py-2 mb-1 on" : "d-flex justify-content-center px-2 py-2 mb-1 off"} style={{borderRadius:'12px', boxShadow:'0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%)'}}>
                        <Dokumen/>                            
                        </div>
                        <b>Data Dokumen</b>                        
                    </div>  
                    <div className="d-flex flex-column mr-5 align-items-center">
                        <div onClick={()=>setPage(4)} role="button" className={page == 4 ? "d-flex justify-content-center px-2 py-2 mb-1 on" : "d-flex justify-content-center px-2 py-2 mb-1 off"} style={{borderRadius:'12px', boxShadow:'0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%)'}}>
                        <Rincian/>                            
                        </div>
                        <b>Barang/Jasa</b>               
                    </div>    
                    <div className="d-flex flex-column mr-5 align-items-center">
                        <div onClick={()=>setPage(5)} role="button" className={page == 5 ? "d-flex justify-content-center px-2 py-2 mb-1 on" : "d-flex justify-content-center px-2 py-2 mb-1 off"} style={{borderRadius:'12px', boxShadow:'0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%)'}}>
                        <InfoTerkait/>                            
                        </div>
                        <b>Info Terkait</b>               
                    </div>  
                    <div className="d-flex flex-column align-items-center">
                        <div onClick={()=>setPage(6)} role="button" className={page == 6 ? "d-flex justify-content-center px-2 py-2 mb-1 on" : "d-flex justify-content-center px-2 py-2 mb-1 off"} style={{borderRadius:'12px', boxShadow:'0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%)'}}>
                        <Checkpoint/>                            
                        </div>
                        <b>Checkpoint</b>               
                    </div>                                                                                                                     
                </div>
                {
                    page != 1 ?
                    <>
                    <div className="d-flex flex-row ml-3">
                        <b>ID Pengajuan : 98765432190</b>
                    </div>                    
                    </>
                    :
                    <>
                    </>
                }                
                <CCard className="mt-2" style={{borderRadius:'12px', boxShadow:'0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%)'}}>
                    <CCardBody>
                        {arrPage[page-1]}
                    </CCardBody>
                </CCard>                 
            </CCardBody>
        </CCard>       
        </>
    )
}

export default New