import {React, useState} from 'react'
import {CFormGroup, CInput, CLabel, CRow, CCol, CSelect, CDataTable, CButton, CModal, CModalHeader, CModalBody, CModalFooter} from '@coreui/react'

const DataKontrak = () =>{
    const [modal, setModal] = useState(false)
    return(
        <>
        <CModal
        show={modal}
        size="md"
        onClose={()=>setModal(false)}
        >
            <CModalHeader closeButton style={{backgroundColor:'#02275d', color:'white'}}><h5>Tambah Rekening Pembayaran</h5></CModalHeader>
            <CModalBody>
                <CRow>
                    <CCol md="12">
                    <CFormGroup>
                        <CLabel>Kode Bank</CLabel>
                        <CSelect>
                            <option value="0">--Select--</option>
                            <option value="008">008 - Bank Mandiri</option>
                            <option value="009">009 - BNI</option>
                            <option value="014">014 - BCA</option>
                        </CSelect>
                    </CFormGroup>                        
                    </CCol>
                </CRow>
                <CRow>
                    <CCol md="12">
                    <CFormGroup>
                        <CLabel>Nama Bank</CLabel>
                        <CInput disabled/>
                    </CFormGroup>                        
                    </CCol>
                </CRow> 
                <CRow>
                    <CCol md="12">
                    <CFormGroup>
                        <CLabel>Nomor Rekening</CLabel>
                        <CInput/>
                    </CFormGroup>                        
                    </CCol>
                </CRow>
                <CRow>
                    <CCol md="12">
                    <CFormGroup>
                        <CLabel>Nama Pemilik</CLabel>
                        <CInput/>
                    </CFormGroup>                        
                    </CCol>
                </CRow>   
                <div className="d-flex flex-row justify-content-end">
                    <div className="mr-2"><CButton onClick={()=>setModal(false)} className="btn btn-success"><i class="far fa-save"></i></CButton></div>
                    <div className="mr-2"><CButton onClick={()=>setModal(false)} className="btn btn-danger"><i class="fas fa-times"></i></CButton></div>
                </div>                                                              
            </CModalBody>
        </CModal>
        <CRow>
            <CCol md="4">
                <CFormGroup>
                    <CLabel>Pembetulan Ke</CLabel>
                    <CInput disabled/>
                </CFormGroup>
            </CCol>
            <CCol md="4">
                <CFormGroup>
                    <CLabel>Nomor PPBJ</CLabel>
                    <CInput disabled/>
                </CFormGroup>
            </CCol>
            <CCol md="4">
                <CFormGroup>
                    <CLabel>Tanggal PPBJ</CLabel>
                    <CInput disabled/>
                </CFormGroup>
            </CCol>                        
        </CRow> 
        <CRow>
            <CCol md="2">
                <CFormGroup>
                    <CLabel>Objek Transaksi</CLabel>
                    <CSelect>
                        <option value="0">--Select--</option>
                        <option value="1">BKP Berwujud</option>
                        <option value="2">BKP Tidak Berwujud</option>
                        <option value="3">JKP</option>
                    </CSelect>
                </CFormGroup>
            </CCol>
            <CCol md="2">
                <CFormGroup>
                    <CLabel>Jenis Transaksi</CLabel>
                    <CSelect>
                        <option value="0">--Select--</option>
                        <option value="1">Perolehan</option>
                        <option value="2">Pengeluaran BKP Berwujud yang Bukan Penyerahan</option>
                        <option value="3">Pemasukan BKP Berwujud yang Bukan Penyerahan</option>
                    </CSelect>
                </CFormGroup>
            </CCol>  
            <CCol md="2">
                <CFormGroup>
                    <CLabel>Asal</CLabel>
                    <CSelect>
                        <option value="0">--Select--</option>
                        <option value="1">Perolehan</option>
                        <option value="2">Pengeluaran BKP Berwujud yang Bukan Penyerahan</option>
                        <option value="3">Pemasukan BKP Berwujud yang Bukan Penyerahan</option>
                    </CSelect>
                </CFormGroup>
            </CCol>  
            <CCol md="2">
                <CFormGroup>
                    <CLabel>Tujuan</CLabel>
                    <CSelect>
                        <option value="0">--Select--</option>
                        <option value="1">Perolehan</option>
                        <option value="2">Pengeluaran BKP Berwujud yang Bukan Penyerahan</option>
                        <option value="3">Pemasukan BKP Berwujud yang Bukan Penyerahan</option>
                    </CSelect>
                </CFormGroup>
            </CCol>     
            <CCol md="4">
                <CFormGroup>
                    <CLabel>BKPTB/JKP melekat dengan BKP berwujud?</CLabel>
                    <CSelect>
                        <option value="0">--Select--</option>
                        <option value="1">Perolehan</option>
                        <option value="2">Pengeluaran BKP Berwujud yang Bukan Penyerahan</option>
                        <option value="3">Pemasukan BKP Berwujud yang Bukan Penyerahan</option>
                    </CSelect>
                </CFormGroup>
            </CCol>                                            
        </CRow>  
        <hr/>
        <div><h5>Kontrak Penyerahan</h5></div>
        <CRow>
            <CCol md="3">
                <CFormGroup>
                    <CLabel>Nomor Kontrak</CLabel>
                    <CInput/>
                </CFormGroup>
            </CCol>
            <CCol md="3">
                <CFormGroup>
                    <CLabel>Tanggal Kontrak</CLabel>
                    <CInput type="date"/>
                </CFormGroup>
            </CCol>
            <CCol md="3">
                <CFormGroup>
                    <CLabel>Nilai Kontrak</CLabel>
                    <CInput/>
                </CFormGroup>
            </CCol>   
            <CCol md="3">
                <CFormGroup>
                    <CLabel>Valuta</CLabel>
                    <CSelect>
                        <option value="0">--Select--</option>
                    </CSelect>
                </CFormGroup>
            </CCol>                                 
        </CRow> 
        <hr/>
        <CRow>
            <CCol md="12">
                <div className="mb-3"><h5>Rekening Pelaku Usaha (Penerima Jasa)</h5></div>
                <div className="d-flex flex-row justify-content-end mb-2"><CButton onClick={()=>setModal(true)} style={{borderRadius:'5px'}} className="btn btn-sm btn-success"><i class="fas fa-plus"></i></CButton></div>
                <CDataTable
                addTableClasses="tablebiru"
                fields={[
                    {key:'no', label:'No'},
                    {key:'kdbank', label:'Kode Bank'},
                    {key:'nmbank', label:'Nama Bank'},                    
                    {key:'norek', label:'Nomor Rekening'},
                    {key:'nmpemilik', label:'Nama Pemilik'},
                    {key:'aksi', label:'Aksi'},
                ]}
                items={[
                    {
                        no:'1',
                        kdbank:'008',
                        nmbank:'BANK MANDIRI',
                        norek:'9987998111231',
                        nmpemilik:'Taufiq M'
                    }
                ]}
                scopedSlots={{
                    'aksi':
                    (item, index)=>{
                        return(
                            <td>
                                <div className="d-flex flex-row">
                                    <div className="mr-1"><CButton className="btn btn-sm" style={{backgroundColor:'#02275d', color:'white'}}><i class="fas fa-eye"></i></CButton></div>
                                    <div className="mr-1"><CButton className="btn btn-sm" style={{backgroundColor:'#517fa4', color:'white'}}><i class="fas fa-edit"></i></CButton></div>
                                    <div className="mr-1"><CButton className="btn btn-sm" style={{backgroundColor:'#f76c6b', color:'white'}}><i class="fas fa-trash-alt"></i></CButton></div>                                                                        
                                </div>
                            </td>
                        )
                    }
                }}
                />
            </CCol>
        </CRow> 
        <CRow>
            <CCol md="12">
                <div className="d-flex flex-row justify-content-end">
                    <CButton className="btn btn-success btn-sm mr-1" style={{borderRadius:'12px'}}><i class="far fa-save"></i> Simpan</CButton>
                </div>
            </CCol>
        </CRow>          
        </>
    )
}

export default DataKontrak