import { CButton } from '@coreui/react'
import {React} from 'react'
import { useHistory } from 'react-router-dom'

const DataCheckpoint = () =>{
    const history = useHistory()
    return(
        <>
        <div className="d-flex flex-row justify-content-start">
        <div className="mr-3"><i class="fas fa-file"></i></div>
        <div><h5>Checkpoint Kelengkapan Data</h5></div>
        </div>
        <div className="card d-flex flex-row justify-content-between mt-4">
            <div className="d-flex flex-row mx-4 my-4">
                <div className="mr-3" style={{color:'green'}}>
                    <i class="fas fa-check fa-3x"></i>
                </div>
                <div className="d-flex flex-column">
                    <div><b>Data Perusahaan</b></div>
                    <div>Data Lengkap</div>
                </div>
            </div>
            <div className="d-flex flex-row mx-4 my-4">
                <div className="mr-3" style={{color:'green'}}>
                    <i class="fas fa-check fa-3x"></i>
                </div>
                <div className="d-flex flex-column">
                    <div><b>Data Kontrak</b></div>
                    <div>Data Lengkap</div>
                </div>
            </div>
            <div className="d-flex flex-row mx-4 my-4">
                <div className="mr-3" style={{color:'green'}}>
                    <i class="fas fa-check fa-3x"></i>
                </div>
                <div className="d-flex flex-column">
                    <div><b>Data Dokumen</b></div>
                    <div>Data Lengkap</div>
                </div>
            </div> 
            <div className="d-flex flex-row mx-4 my-4">
                <div className="mr-3" style={{color:'green'}}>
                    <i class="fas fa-check fa-3x"></i>
                </div>
                <div className="d-flex flex-column">
                    <div><b>Barang/Jasa</b></div>
                    <div>Data Lengkap</div>
                </div>
            </div> 
            <div className="d-flex flex-row mx-4 my-4">
                <div className="mr-3" style={{color:'green'}}>
                    <i class="fas fa-check fa-3x"></i>
                </div>
                <div className="d-flex flex-column">
                    <div><b>Info Terkait</b></div>
                    <div>Data Lengkap</div>
                </div>
            </div>                                                
        </div>
        <div className="card d-flex flex-column px-4 py-4">
            <div><h5><b>Disclaimer</b></h5></div>
            <div><p>1. Dengan ini saya menyatakan bertanggung jawab atas kebenaran hal-hal yang diberitahukan dalam dokumen ini</p></div>
        </div>
        <div className="d-flex flex-row justify-content-between">
            <div className="d-flex flex-row">
                <div className="mr-2"><input type="checkbox"></input></div>
                <div>Saya Menyetujui</div>
            </div>
            <div className="d-flex flex-row">
                <div className="mr-2">
                    <i class="fas fa-print"></i>                    
                </div>
                <div>
                    Cetak Laporan                    
                </div>
            </div>
        </div>
        <div className="d-flex flex-row justify-content-between mt-4">
            <div>
                <CButton onClick={()=>history.push('/ppbj')} className="btn btn-sm btn-light" style={{borderRadius:'12px'}}><i class="fas fa-arrow-left"></i> Kembali</CButton>
            </div>
            <div className="d-flex flex-row">
                <div className="mr-3">
                    <CButton onClick={()=>history.push('/ppbj')} className="btn btn-sm btn-light" style={{borderRadius:'12px'}}><i class="fas fa-save"></i> Simpan Draft</CButton>                    
                </div>
                <div>
                    <CButton onClick={()=>history.push('/ppbj')} className="btn btn-sm" style={{backgroundColor:'#4dbd74', color:'white', borderRadius:'12px'}}><i class="fas fa-check"></i> Kirim</CButton>
                </div>
            </div>            
        </div>
        </>
    )
}

export default DataCheckpoint