import { CButton, CInput, CRow, CCol, CDataTable, CModalHeader, CModal, CModalBody, CFormGroup, CLabel, CSelect, CInputFile, CBadge} from '@coreui/react'
import {React, useState} from 'react'

const DataDokumen = () =>{
    const [modal, setModal] = useState(false)
    return(
        <>
        <CModal
        show={modal}
        size="md"
        onClose={()=>setModal(false)}
        >
            <CModalHeader closeButton style={{backgroundColor:'#02275d', color:'white'}}><h5>Tambah Data Dokumen</h5></CModalHeader>
            <CModalBody>
                <CRow>
                    <CCol md="12">
                        <CFormGroup>
                            <CLabel>Kategori Dokumen</CLabel>
                            <CInput disabled value="Dokumen PPBJ"/>
                        </CFormGroup>
                    </CCol>
                </CRow>
                <CRow>
                    <CCol md="12">
                        <CFormGroup>
                            <CLabel>Kode Dokumen</CLabel>
                            <CSelect>
                                <option value="0">--Select--</option>
                                <option value="000000">000000 - Kontrak PPBJ</option>
                                <option value="999999">999999 - Dokumen Lainnya</option>
                            </CSelect>
                        </CFormGroup>
                    </CCol>
                </CRow>  
                <CRow>
                    <CCol md="12">
                        <CFormGroup>
                            <CLabel>Nomor Dokumen</CLabel>
                            <CInput/>
                        </CFormGroup>
                    </CCol>
                </CRow>
                <CRow>
                    <CCol md="12">
                        <CFormGroup>
                            <CLabel>Tanggal Dokumen</CLabel>
                            <CInput type="date"/>
                        </CFormGroup>
                    </CCol>
                </CRow>   
                <CRow>
                    <CCol md="12">
                        <CFormGroup>
                            <CLabel>Dokumen</CLabel>
                            <CCol md="9">
                                <CInputFile custom id="custom-file-input"/>
                                <CLabel htmlFor="custom-file-input" variant="custom-file">
                                Choose file...
                                </CLabel>                                
                            </CCol>
                            <CBadge style={{backgroundColor:'#f76c6b', color:'white'}}>Tipe File : PDF. Maksimum Upload File : 25 MB</CBadge>
                        </CFormGroup>
                    </CCol>
                </CRow>  
                <div className="d-flex flex-row justify-content-end">
                    <div className="mr-2"><CButton onClick={()=>setModal(false)} className="btn btn-success"><i class="far fa-save"></i></CButton></div>
                    <div className="mr-2"><CButton onClick={()=>setModal(false)} className="btn btn-danger"><i class="fas fa-times"></i></CButton></div>
                </div>                                                                          
            </CModalBody>
        </CModal>
        <CRow>
            <CCol md="12">
                <div className="d-flex flex-row justify-content-end mb-2">
                    <div><CButton onClick={()=>setModal(true)} style={{borderRadius:'5px'}} className="btn btn-sm btn-success"><i class="fas fa-plus"></i></CButton></div>                    
                </div>
                <CDataTable
                addTableClasses="tablebiru"
                fields={[
                    {key:'no', label:'No'},
                    {key:'ktgrdok', label:'Kategori Dokumen'},
                    {key:'kddok', label:'Kode Dokumen'},
                    {key:'nodok', label:'Nomor Dokumen'},
                    {key:'tgldok', label:'Tanggal Dokumen'},
                    {key:'dok', label:'Dokumen'},  
                    {key:'aksi', label:'Aksi'},                                                            
                ]}
                />
            </CCol>
        </CRow>
        </>
    )
}

export default DataDokumen