import { CFormGroup, CLabel, CRow, CSelect, CCol, CInput, CDataTable, CButton, CModalHeader, CModal, CModalBody} from '@coreui/react'
import {React, useState} from 'react'

const DataInfoTerkait = () =>{
    const [modal, setModal] = useState(false)
    return(
        <>
        <CModal
        show={modal}
        size="lg"
        onClose={()=>setModal(false)}
        >
            <CModalHeader><h5>Tambah Kemasan</h5></CModalHeader>
            <CModalBody>
                <CRow>
                    <CCol md="6">
                        <CLabel>Kode Kemasan</CLabel>
                        <CSelect>
                            <option value="00">--Select--</option>
                        </CSelect>
                    </CCol>
                    <CCol md="6">
                        <CLabel>Merek Kemasan</CLabel>
                        <CInput/>
                    </CCol>                    
                </CRow>
                <CRow>
                    <CCol md="6">
                        <CLabel>Nomor Kemasan</CLabel>
                        <CInput/>
                    </CCol>
                    <CCol md="3">
                        <CLabel>Jumlah Kemasan</CLabel>
                        <CInput/>
                    </CCol> 
                    <CCol md="3">
                        <CLabel>Merek Kemasan</CLabel>
                        <CSelect>
                            <option value="00">--Select--</option>
                        </CSelect>
                    </CCol>                                        
                </CRow>  
                <div className="d-flex flex-row justify-content-end mt-3">
                    <div className="mr-2"><CButton onClick={()=>setModal(false)} className="btn btn-success"><i class="far fa-save"></i></CButton></div>
                    <div className="mr-2"><CButton onClick={()=>setModal(false)} className="btn btn-danger"><i class="fas fa-times"></i></CButton></div>
                </div>                               
            </CModalBody>
        </CModal>
        <div><h5>Informasi BKP Berwujud yang Terkait dengan Perolehan / Penyerahan BKPTB/JKP</h5></div>
        <CRow>
            <CCol md="4">
                <CFormGroup>
                    <CLabel>Cara Pengangkutan</CLabel>
                    <CSelect>
                        <option value="00">--Select--</option>
                        <option value="01">Laut</option>
                        <option value="02">Kereta Api</option>
                        <option value="03">Darat</option>  
                        <option value="04">Udara</option>
                        <option value="05">Pos</option>
                        <option value="06">Multimoda</option>
                        <option value="07">Lainnya</option>                                                                        
                    </CSelect>
                </CFormGroup>
            </CCol>
            <CCol md="4">
                <CFormGroup>
                    <CLabel>Nama Sarana Angkut</CLabel>
                    <CInput/>
                </CFormGroup>
            </CCol>
            <CCol md="4">
                <CFormGroup>
                    <CLabel>Perusahaan Pengangkut Barang</CLabel>
                    <CInput/>
                </CFormGroup>
            </CCol>                        
        </CRow>
        <CRow>
            <CCol md="4">
                <CFormGroup>
                    <CLabel>No BL/AWB/DO</CLabel>
                    <CInput/>
                </CFormGroup>
            </CCol>
            <CCol md="4">
                <CFormGroup>
                    <CLabel>Rencana Jangka Waktu Penggunaan Barang</CLabel>
                    <CInput type="date"/>
                </CFormGroup>
            </CCol>
            <CCol md="4">
                <CFormGroup>
                    <CLabel>Tujuan Penggunaan Barang</CLabel>
                    <CInput/>
                </CFormGroup>
            </CCol>                        
        </CRow>   
        <CRow>
            <CCol md="6">
                <CFormGroup>
                    <CLabel>No. Dokumen Pabean</CLabel>
                    <CInput/>
                </CFormGroup>
            </CCol>
            <CCol md="6">
                <CFormGroup>
                    <CLabel>Tanggal Dokumen Pabean</CLabel>
                    <CInput type="date"/>
                </CFormGroup>
            </CCol>            
        </CRow>  
        <hr/>
        <div><h5>Kegiatan</h5></div>  
        <CRow>
            <CCol md="3">
                <CLabel>Kegiatan</CLabel>
                <CInput value="Muat" disabled/>
            </CCol>
            <CCol md="3">
                <CLabel>Kode Pelabuhan/Tempat</CLabel>
                <CSelect>
                    <option value="0">--Select--</option>
                </CSelect>
            </CCol>
            <CCol md="3">
                <CLabel>Pelabuhan/Tempat</CLabel>
                <CInput disabled/>
            </CCol> 
            <CCol md="3">
                <CLabel>Negara</CLabel>
                <CInput disabled/>
            </CCol>                                    
        </CRow> 
        <CRow>
            <CCol md="3">
                <CLabel>Kegiatan</CLabel>
                <CInput value="Muat" disabled/>
            </CCol>
            <CCol md="3">
                <CLabel>Kode Pelabuhan/Tempat</CLabel>
                <CSelect>
                    <option value="0">--Select--</option>
                </CSelect>
            </CCol>
            <CCol md="3">
                <CLabel>Pelabuhan/Tempat</CLabel>
                <CInput disabled/>
            </CCol> 
            <CCol md="3">
                <CLabel>Negara</CLabel>
                <CInput disabled/>
            </CCol>                                    
        </CRow>
        <hr/>   
        <div><h5>Kemasan</h5></div>  
        <div className="d-flex flex-row justify-content-end mb-2">
            <div><CButton onClick={()=>setModal(true)} style={{borderRadius:'5px'}} className="btn btn-sm btn-success"><i class="fas fa-plus"></i></CButton></div>                    
        </div>
        <CDataTable
        addTableClasses="tablebiru"
        fields={[
            {key:'no', label:'No'},
            {key:'kdkemasan', label:'Kode Kemasan'},
            {key:'merkkemasan', label:'Merk Kemasan'},
            {key:'nokemasan', label:'Nomor Kemasan'},
            {key:'jmlkemasan', label:'Jumlah Kemasan'},
            {key:'aksi', label:'Aksi'},
        ]}
        /> 
        <CRow>
            <CCol md="12">
                <div className="d-flex flex-row justify-content-end">
                    <CButton className="btn btn-success btn-sm mr-1" style={{borderRadius:'12px'}}><i class="far fa-save"></i> Simpan</CButton>
                </div>
            </CCol>
        </CRow>           
        </>
    )
}

export default DataInfoTerkait